* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}


.card {
  background: rgb(255, 252, 65);
  border-radius: 2px;
  height: 220px;
  margin: 6px;
  margin-top: 20px;
  position: relative;
  width: 230px;
  color: black;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-shadow: 0 3px 6px rgba(255, 103, 128, 0.801), 0 3px 6px rgba(255, 103, 128, 0.527);
  text-align: left;
}

.card:hover{
  height:225px;
  width:240px;
  box-shadow: 0 3px 6px rgb(0, 0, 0), 0 3px 6px rgb(0, 0, 0);
}

.card > .img-container {
  height: 95%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 65%
}

.card > .content {
  font-family: 'Lilita One', cursive;
  font-size: 10px;
  text-align: center;
}
.score-board{
    font-family: 'Coiny', cursive;
    width: 100%;
    height: 80px;
    text-align: center;
    font-size: 30px;
    background-color: rgb(255, 103, 129);
    border-radius: 10px;
    color: #2A77AE;
    padding-top: 10px;
    margin-bottom: 50px;
    text-shadow: 0 3px 6px rgb(0, 0, 0), 0 3px 6px rgb(0, 0, 0);
}

.collapse{
    height: 200px;
}
.title {
  font-family: 'Coiny', cursive;
  width: 100%;
  height: 160px;
  text-align: center;
  font-size: 60px;
  background-color: rgb(255, 103, 129);
  border-radius: 10px;
  color: #2A77AE;
  padding-top: 10px;
  margin-bottom: 50px;
  text-shadow: 0 3px 6px rgb(0, 0, 0), 0 3px 6px rgb(0, 0, 0);

}
.wrapper {
  padding-top: 50px;
  background: rgb(52, 157, 255);
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

h2{
    font-size: 25px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

